.signIn {
    max-width: 300px;
    padding: 70px;
    margin-left: auto;
    margin-right: auto;
    background: rgba(0, 0, 0, 0.85);
}

.signIn > form {
    display: grid;
    flex-direction: column;
}

.signIn > form > input {
    outline-width: 0;
    height: 40px;
    margin-bottom: 14px;
    border-radius: 5px;
    border: none;
    padding: 5px 15px;
}

.signIn > form > button {
    padding: 16px 20px;
    font-size: 1rem;
    color: white;
    border-radius: 5px;
    background-color: red;
    margin-top: 20px;
    cursor: pointer;
}

.signIn > form > h1 {
    text-align: left;
    margin-bottom: 25px;
}

.signIn > form > h4 {
    text-align: left;
    margin-top: 25px;
}

.signIn__grey {
    color: grey;
}

.signIn__link:hover {
    cursor: pointer;
    text-decoration: underline;
}