.nav {
    position: fixed;
    top: 0;
    padding: 20px;
    width: 100%;
    height: 30px;
    z-index: 100;
    transition-timing-function: ease-in;
    transition: all 0.5s;
}


.nav__background {
    background-color: black;
}

.nav__content {
    display: flex;
    justify-content: space-between;
}

.nav__logo {
    position: fixed;
    top: 18px;
    left: 0;
    width: 80px;
    object-fit: contain;
    padding-left: 20px;
    cursor: pointer;
}

.nav__avatar {
    position: fixed;
    right: 20px;
    width: 30px;
    cursor: pointer;
}