.profile {
    height: 100vh;
    background-color: black;
    color: white;
}

.profile__body {
    display: flex;
    flex-direction: column;
    width: 50%;
    margin-left: auto;
    margin-right: auto;
    max-width: 800px;
    padding-top: 8%;
}

.profile__info {
    display: flex;
}

.profile__details {
    color: white;
    margin-left: 25px;
    flex: 1;
}

.profile__body > h1 {
    font-size: 60px;
    font-weight: 400;
    border-bottom: 1px solid #282c2d;
    margin-bottom: 20px;
}

.profile__details > h2 {
    background-color: grey;
    padding: 15px;
    font-size: 15px;
    padding-left: 20px;
}

.profile__info > img {
    height: 100px;
}

.profile__signOut {
    padding: 10px 20px;
    font-size: 1rem;
    margin-top: 5%;
    width: 100%;
    color: #fff;
    background-color: chocolate;
    border: none;
    cursor: pointer;
}

.profile__teams {
    margin-top: 20px;
}

.profile__teams > h3 {
    border-bottom: 1px solid #282c2d;
    padding-bottom: 10px;
}